@font-face{
  font-family: "starwarsMain";
  src: url("./fonts/Starjedi.ttf");
}

@font-face{
  font-family: "starwarsSecond";
  src: url("./fonts/Starjhol.ttf");
}

.App {
  font-family: starwarsMain;
  letter-spacing: 1.5;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  height:90vh;
  
}

.img{
  width: 100vw;
  height: 50vh;
}

hr{
  width:100%;
}

h3{
  font-family: starwarsSecond;
}

button {
  font-family: starwarsSecond;
  background-color: black;
  border-radius: 10px;
  color: white;
  font-size: 32px;
  padding: 0 25px 5px 25px;
  }
  
  button:hover {
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0.9);
  box-shadow: 0px 4px 8px 0px rgba(0, 0, 0, 0.75);
  transition-duration: 300ms;
  }
  
  button:active {
  transform: scale(0.95);
  }

h1{
  padding: 0 20px;
}